	export default {
		data() {
			return {
				title:"", //标题
				date:"", //发布时间
				content:"",//内容
				id:''
			}
		},
		onLoad(options) {
            this.id=options.id
			this.loadData()
		},
		methods:{
			loadData(){
				let parmas={
					id:this.id
				}
				console.log(parmas)
			   this._reqw.ipost(parmas,'messageDetails').then(res=>{
				   res.result==0?(this.title=res.title,this.date=res.date,this.content=res.content):this.$api.tip(res.resultNote)
			   }).catch(err=>{})
			}
		}
	}
